#btnDel {
    position: absolute;
    width: 60px;
    height: 50px;
    background-color: #e4e8eb;
    border-radius: 25px;
    margin: -60px 200px;
    text-align: center;   
    box-shadow: 2px 2px 5px #ccd0d3, 2px 2px 2px #eff3e6 inset;
}
#textBtnDel {
    font-size: 18px;
    margin: 0 auto;
    color: #000000;
    padding: 13px 0;
    border-radius: 25px;
}

@media screen and (max-width: 992px) {
    #btnDel {
        position: absolute;
        width: 7.33vw;
        height: 4.28vh;
        background-color: #e4e8eb;
        border-radius: 4vw;
        margin: -5.14vh 24.46vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #ccd0d3, 2px 2px 2px #eff3e6 inset;
    }
    #textBtnDel {
        font-size: 1.9vh;
        margin: 0 auto;
        color: #000000;
        padding: 0.85vh 0;
        border-radius: 10vw;
    }
}

@media screen and (max-width: 420px) {
    #btnDel {
        position: absolute;
        width: 15vw;
        height: 5.5vh;
        background-color: #e4e8eb;
        border-radius: 10vw;
        margin: -6.66vh 48.75vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #ccd0d3, 2px 2px 2px #eff3e6 inset;
    }
    #textBtnDel {
        font-size: 1.9vh;
        margin: 0 auto;
        color: #000000;
        padding: 1.42vh 0;
        border-radius: 10vw;
    }
}