#btnC {
    display: inline-block;
    width: 60px;
    height: 50px;
    background-color: #fdb82c;
    border-radius: 25px;
    margin: 10px 16px;
    text-align: center;   
    box-shadow: 2px 2px 5px #fcc352, 2px 2px 2px #f7d083 inset;
}
#textBtnC {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 21px;
    margin: 0 auto;
    color: #ffffff;
    padding: 13px 0;
    border-radius: 25px;
}

@media screen and (max-width: 992px) {
    #btnC {
        display: inline-block;
        width: 7.33vw;
        height: 4.28vh;
        background-color: #fdb82c;
        border-radius: 4vw;
        margin: 0.86vh 1.96vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #fcc352, 2px 2px 2px #f7d083 inset;
    }
    #textBtnC {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2.3vh;
        margin: 0 auto;
        color: #ffffff;
        padding: 0.83vh 0;
        border-radius: 4vw;
    }
}

@media screen and (max-width: 420px) {
    #btnC {
        display: inline-block;
        width: 15vw;
        height: 5.5vh;
        background-color: #fdb82c;
        border-radius: 10vw;
        margin: 1.16vh 3.75vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #fcc352, 2px 2px 2px #f7d083 inset;
    }
    #textBtnC {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2.3vh;
        margin: 0 auto;
        color: #ffffff;
        padding: 1.42vh 0;
        border-radius: 10vw;
    }
}