#theme {
    width: 400px;
    height: 80px;
}

#btnTheme {
    width: 60px;
    height: 30px;
    background-color: #969ea2;
    margin: 25px 25px;
    display: inline-block;
    border-radius: 15px;
    box-shadow: 2px 2px 5px #aaabb3, 2px 2px 2px #fff8e3 inset;
}

#position {
    width: 24px;
    height: 24px;
    background-color: #f6f6f8;
    border: 2px solid #969ea2;
    border-radius: 50%;
    margin-top: 2px;
    
}

#themeText {
    display: inline-block;
    margin: 29.5px 0;
    position: absolute;
    font-size: 18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #9297a1;

}

@media screen and (max-width: 992px) {
    #theme {
        width: 45vw;
        height: 6vh;
    }
    #btnTheme {
        width: 7vw;
        height: 3.5vh;
        background-color: #969ea2;
        margin: 1vh 3vw;
        display: inline-block;
        border-radius: 4vw;
        box-shadow: 2px 2px 5px #aaabb3, 2px 2px 2px #fff8e3 inset;
    }
    
    #position {
        width: 3.5vw;
        height: 2.9vh;
        background-color: #f6f6f8;
        border: 2px solid #969ea2;
        border-radius: 50%;
        margin-top: 0.25vh;
        
    }
    
    #themeText {
        display: inline-block;
        margin: 1.8vh 0;
        position: absolute;
        font-size: 2vh;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        color: #9297a1;
    
    }
}

@media screen and (max-width: 420px) {
    #theme {
        width: 90vw;
        height: 9vh;
    }
    #btnTheme {
        width: 14.6vw;
        height: 3.29vh;
        background-color: #969ea2;
        margin: 2.74vh 6.07vw;
        display: inline-block;
        border-radius: 4vw;
        box-shadow: 2px 2px 5px #aaabb3, 2px 2px 2px #fff8e3 inset;
    }
    
    #position {
        width: 5.83vw;
        height: 2.63vh;
        background-color: #f6f6f8;
        border: 2px solid #969ea2;
        border-radius: 50%;
        margin-top: 0.25vh;
        
    }
    
    #themeText {
        display: inline-block;
        margin: 3.23vh 0;
        position: absolute;
        font-size: 2vh;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        color: #9297a1;
    
    }
}