body {
    margin: 0;
}

#root {
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(#e6e6e9, #dfdfdf);
    padding-top: 100px;
    padding-bottom: 100px;
}


#mainCase {
    width: 400px;
    height: 785px;
    background-image: linear-gradient(#f6f6f8, #f1f1f1);
    margin: auto;
    position: relative;
    border-radius: 30px;
    box-shadow: 10px 10px 20px #aeaeb1, 0 0 20px #e6e4df;
    
}

#display {
    width: 400px;
    height: 360px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
}

#showBox {
    width: 340px;
    margin:  0 30px;
    padding: 15px 0;
    line-height: 220px;
    height: 150px;
    text-align: end;
    font-size: 20px;
}
#show {
    line-height: 1.5;
    display: inline-block;
    vertical-align: bottom; 
    color: #5e6871; 
}

#resultBox {
    width: 340px;
    padding: 0 30px;
    line-height: 70px;
    height: 70px;
    font-size: 30px;
    text-align: end;
}

#resuslt {
    line-height: 1.5;
    display: inline-block;
    vertical-align: middle;
    color: #5e6871; 
}

#calc {
    display: none;
}

#test {
    display: none;
}

#buttonsCase {
    width: 368px;
    height: 425px;
    margin: auto;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
}

@media screen and (max-width: 992px) {
    body {
        margin: 0;
    }

    #root {
        width: 100vw;
        height: 100vh;
        background-image: linear-gradient(#e6e6e9, #dfdfdf);
        padding-top: 0;
        padding-bottom: 0;
    }

    #mainCase {
