    }

    #show {
        line-height: 1.5;
        display: inline-block;
        vertical-align: bottom; 
        color: #5e6871;
        margin: 0; 
    }

    #resultBox {
        width: 40vw;
        height: 6vh;
        margin: 0 2.5vw;
        padding: 0;
        line-height: 8vh;
        font-size: 2.6vh;
