    }

    #result {
        line-height: 1.5;
        display: inline-block;
        vertical-align: middle;
        color: #5e6871; 
        margin: 0;
        padding-right: 1vw;
    }

    #calc {
        display: none;
    }
    
    #test {
        display: none;
    }
    
    #buttonsCase {
