.btnNumber {
    display: inline-block;
    width: 60px;
    height: 50px;
    background-color: #f4f5f7;
    border-radius: 25px;
    margin: 10px 16px;
    text-align: center;   
    box-shadow: 2px 2px 5px #d7d9d8, 2px 2px 2px #fcf8f8 inset;
}

#btnHidden {
    display: inline-block;
    width: 60px;
    height: 50px;
    border-radius: 25px;
    margin: 10px 16px;
    text-align: center;    
}
.textBtnNumber {
    font-size: 20px;
    margin: 0 auto;
    color: #516975;
    padding: 13.5px 0;
    border-radius: 25px;
}

@media screen and (max-width: 992px) {
    .btnNumber {
        display: inline-block;
        width: 7.33vw;
        height: 4.28vh;
        background-color: #f4f5f7;
        border-radius: 4vw;
        margin: 0.86vh 1.96vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #d7d9d8, 2px 2px 2px #fcf8f8 inset;
    }

    #btnHidden {
        display: inline-block;
        width: 7.33vw;
        height: 4.28vh;
        border-radius: 4vw;
        margin: 0.86vh 1.96vw;
        text-align: center;
    }
    .textBtnNumber {
        font-size: 2.3vh;
        margin: 0 auto;
        color: #516975;
        padding: 0.83vh 0;
        border-radius: 4vw;
    }
}

@media screen and (max-width: 420px) {
    .btnNumber {
        display: inline-block;
        width: 15vw;
        height: 5.5vh;
        background-color: #f4f5f7;
        border-radius: 10vw;
        margin: 1.16vh 3.75vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #d7d9d8, 2px 2px 2px #fcf8f8 inset;
    }

    #btnHidden {
        display: inline-block;
        width: 15vw;
        height: 5.5vh;
        border-radius: 10vw;
        margin: 1.16vh 3.75vw;
        text-align: center;
    }
    .textBtnNumber {
        font-size: 2.2vh;
        margin: 0 auto;
        color: #516975;
        padding: 1.42vh 0;
        border-radius: 10vw;
    }
}
