#btnEqual {
    position: absolute;
    width: 60px;
    height: 120px;
    background-color: #c55cda;
    border-radius: 25px;
    margin: -60px 292px;
    text-align: center;
    box-shadow: 2px 2px 5px #9b83bf, 2px 2px 2px #e1a5f0 inset;
}

#textBtnEqual {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 40px;
    margin: 0 auto;
    color: #ffffff;
    padding: 37px 0;
    border-radius: 25px;
}

@media screen and (max-width: 992px) {
    #btnEqual {
        position: absolute;
        width: 7.33vw;
        height: 10.28vh;
        background-color: #c55cda;
        border-radius: 4vw;
        margin: -5.14vh 35.71vw;
        text-align: center;
        box-shadow: 2px 2px 5px #9b83bf, 2px 2px 2px #e1a5f0 inset;
    }
    
    #textBtnEqual {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 4.5vh;
        margin: 0 auto;
        color: #ffffff;
        padding: 2.55vh 0;
        border-radius: 4vw;
    }
}

@media screen and (max-width: 420px) {
    #btnEqual {
        position: absolute;
        width: 15vw;
        height: 13.32vh;
        background-color: #c55cda;
        border-radius: 10vw;
        margin: -6.66vh 71.25vw;
        text-align: center;
        box-shadow: 2px 2px 5px #9b83bf, 2px 2px 2px #e1a5f0 inset;
    }
    
    #textBtnEqual {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 4.5vh;
        margin: 0 auto;
        color: #ffffff;
        padding: 4.05vh 0;
        border-radius: 10vw;
    }
}