.btnOtherSymbol {
    display: inline-block;
    width: 60px;
    height: 50px;
    background-color: #f6f0e4;
    border-radius: 25px;
    margin: 10px 16px;
    text-align: center;   
    box-shadow: 2px 2px 5px #e6d3ab, 2px 2px 2px #f3f7ea inset;
}
#textBtnOtherSymbol {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 21px;
    margin: 0 auto;
    color: #b47d2a;
    padding: 13.5px 0;
    border-radius: 25px;
}
#sup {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 21px;
    margin: 0 auto;
    color: #b47d2a;
    padding: 10.5px 0;
    border-radius: 25px;
}


@media screen and (max-width: 992px) {
    .btnOtherSymbol {
        display: inline-block;
        width: 7.33vw;
        height: 4.28vh;
        background-color: #f6f0e4;
        border-radius: 4vw;
        margin: 0.86vh 1.96vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #e6d3ab, 2px 2px 2px #f3f7ea inset;
    }
    #textBtnOtherSymbol {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2.3vh;
        margin: 0 auto;
        color: #b47d2a;
        padding: 0.83vh 0;
        border-radius: 4vw;
    }
    #sup {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2vh;
        margin: 0 auto;
        color: #b47d2a;
        padding: 0.83vh 0;
        border-radius: 4vw;
    }
}

@media screen and (max-width: 420px) {
    .btnOtherSymbol {
        display: inline-block;
        width: 15vw;
        height: 5.5vh;
        background-color: #f6f0e4;
        border-radius: 10vw;
        margin: 1.16vh 3.75vw;
        text-align: center;   
        box-shadow: 2px 2px 5px #e6d3ab, 2px 2px 2px #f3f7ea inset;
    }
    #textBtnOtherSymbol {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2.3vh;
        margin: 0 auto;
        color: #b47d2a;
        padding: 1.42vh 0;
        border-radius: 10vw;
    }
    #sup {
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 2.3vh;
        margin: 0 auto;
        color: #b47d2a;
        padding: 1.15vh 0;
        border-radius: 10vw;
    }
}